<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import moment from 'moment'

export default {
  data () {
    return {
      self: this,
      columns: [
          {
              title: 'ID',
              key: 'uid',
              align: 'center',
          },
          {
              title: '管理员账户',
              key: 'username',
              align: 'center',
          },
          {
              title: '最后登陆时间',
              key: 'lasttime',
              align: 'center',
          },
          {
              title: '最后登录IP',
              key: 'lastip',
              align: 'center',
          },
          {
              title: '登录次数',
              key: 'logincount',
              align: 'center',
          },
          {
            title: '状态',
            key: 'status',
            align: 'center',
            render: (h, params) => {
              const status = params.row.status;
              const color = status == 1 ? 'success' : 'error';
              const text = status == 1 ? '正常' : '停用';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
            title: '游戏',
            key: 'gameDay',
            align: 'center',
          },
          {
            title: '应用',
            key: 'appDay',
            align: 'center',
          },
          {
            title: '新闻',
            key: 'newsDay',
            align: 'center',
          },
          {
            title: '操作记录',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "查看",
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.show(params)
                    }
                  }
                }, 'View')
              ]);
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "修改"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.modify(params)
                    }
                  }
                }, 'View')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
      totalPages: 0,
      adminModel: '',
      statusModel: "0",
      params: {}
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token, "page": "0", "per_page": 12, "name": "", "type": 1};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData: function(params) {
      api.adminList(params).then(res => {
        if(res.code === 0) {
          this.data = res.data.adminlist
          this.data.forEach(itm => {
            itm.lasttime = moment(this.data.lasttime).format("YYYY-M-DD k:mm");
            itm.gameDay = itm.ydayGame + ' - ' + itm.tdayGame
            itm.appDay = itm.ydayApp + ' - ' + itm.tdayApp
            itm.newsDay = itm.ydayNews + ' - ' + itm.tdayNews
          })
          this.totalPages = res.data.totlerows
          this.isLoading = false
        } else {
          this.$Notice.error({title: res.msg})
        }
        console.log(res, this.data)
      }).catch(err => {
        this.$Notice.error({title: err})
      })
    },
    setNewData: function() {
      this.params.name = this.adminModel;
      this.params.type = this.statusModel;
      console.log(this.params)
      this.getData(this.params);
    },
    show: function(params) {
      this.$router.push({
        path: '/admin/adminlog',
        query: {uid: params.row.uid}
      })
    },
    remove: function(index) {
      this.data.splice(index, 1);
    },
    modify: function(params) {
      // console.log(params.row)
      const uid = params.row.uid,
            admin = params.row.username;
      params.row.uid == 1 ? this.$Notice.warning({title: admin+'不能被修改'}) : this.$router.push({path:'/admin/adminedit',query: {"uid":uid }});
    },
    changePage: function(page) {
      // console.log(page)
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      // console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
  },
  watch: {
    statusModel: function() {
      this.params.type = this.statusModel == "0" ? 1 : this.statusModel == "2" ?  "2" : "1";
      this.getData(this.params);
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-baseball" size=24 />平台管理员</h3>
    <div class="searchBar">
      <Button type="primary" to="/admin/adminadd">添加账户</Button>
      <Input v-model="adminModel" prefix="ios-contact" placeholder="请输入管理员账户" style="width: auto" />
      <Select v-model="statusModel" style="width:100px">
        <Option value="0">状态</Option>
        <Option value="1">正常</Option>
        <Option value="2">停用</Option>
      </Select>
      <Button type="primary" @click="setNewData()">
        <Icon type="ios-search" :size=18 />
        搜索
      </Button>
    </div>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=12 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
    
</template>
<style lang="scss" scoped>
  .searchBar {
    margin-bottom: 20px;
    .ivu-btn {
      margin-right: 20px;
    }
    .ivu-input-wrapper {
      margin-right: 20px;
    }
    .ivu-select {
      margin-right: 20px;
    }
  }
</style>
